.navlink {
  text-decoration: none;
  display: inline-block;
  margin-right: 3px;
  padding: 5px;
  color: inherit;
  font-weight: inherit;
  font-size: min(3vh, 35px);
}

.navlink:hover {
  color: darkgray;
}

/* For phones */
@media screen and (max-width: 500px) {
  .navlink {
    font-size: 5vw;
  }
}
